import React, { useState, useEffect, useCallback } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';

export default function Applications() {
  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/applications', { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.applications || [];

      setresultlist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token]);

  const revokeClient = useCallback((clientId) => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/applications/' + clientId, { headers: { Authorization: "Bearer " + token }, method: 'DELETE'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success(clientId + ' removed.');
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [token]);

  return (
    <React.Fragment>
    <h3>Apps</h3>
    <Table striped bordered>
      <thead>
        <tr>
          <th>Name</th>
          <th>Description</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      { resultlist.map(app => { return (
        <tr key={app.clientId}>
          <td><a href={app.homepage}>{app.name}</a></td>
          <td>{app.description}</td>
          <td><Button variant="outline-danger" onClick={(event) => {event.preventDefault();revokeClient(app.clientId)}}>Revoke</Button></td>
        </tr>
      )})}
      </tbody>
    </Table>
    </React.Fragment>
  );
}
