import React, { useEffect, useState } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { Link } from "react-router-dom";
import { toast } from 'react-toastify';

export default function Sites() {

  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites', { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.sites || [];

      setresultlist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token]);

  return (
    <React.Fragment>
    <h3>Sites</h3>
    <Table striped bordered>
      <thead>
        <tr>
          <th>Name</th>
          <th>City</th>
          <th>Region</th>
          <th>DICOMweb</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      { resultlist.map(site => { return (
        <tr key={site.siteId}>
          <td>{site.name}</td>
          <td>{site.city}</td>
          <td>{site.region}</td>
          <td><a href={process.env.REACT_APP_ESPRESSOAPI_URL + '/api/dicomweb/' + site.siteId}>{process.env.REACT_APP_ESPRESSOAPI_URL + '/api/dicomweb/' + site.siteId}</a></td>
          <td><Button variant="outline-primary" as={Link} to={'/sites/' + site.siteId}>Edit</Button></td>
        </tr>
      )})}
      </tbody>
    </Table>
    <Button as={Link} to='/sites/new'>New</Button>
    </React.Fragment>
  );
}
