import React, { useState, useEffect } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';
import { useParams } from "react-router-dom";
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { LazyLoadComponent, trackWindowScroll } from 'react-lazy-load-image-component';
import LazyImage from '../parts/lazyimage';
import * as dayjs from 'dayjs';
var localizedFormat = require('dayjs/plugin/localizedFormat')
dayjs.extend(localizedFormat)

const divStyle = {
  width: 200,
  height:200,
  display: "inline-block",
};

const Gallery = ({ fullstudy, scrollPosition }) => (
  <div>
    {fullstudy.series.map((series) => { return (
     series.instances.map((instance) =>
      <div key={instance.instanceId} style={divStyle}>
        <LazyLoadComponent scrollPosition={scrollPosition} placeholder={<img src='/text.png' alt='not loaded' />} >
          <LazyImage key={instance.instanceId} instanceId={instance.instanceId} isThumbnail={true}/>
        </LazyLoadComponent>
      </div>
    ))})}
  </div>
);


function StudyParams({patientStudyId}) {
  const [token, setToken] = useLocalStorage('token', "");
  const [fullstudy, setfullstudy] = useState({series: [], apps: []});
  const siteRegion = useSelector((state) => state.site.region);
  const siteId = useSelector((state) => state.site.siteId);
  const [destinations, setdestinations] = useState([]);

  useEffect(() => {
    var regionurl = null;
    switch(siteRegion) {
      case 'us':
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL + '/api/studies/' + patientStudyId + '/getFullStudy';
        break;
      case 'tw':
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL_TW + '/api/studies/' + patientStudyId + '/getFullStudy';
        break;
      default:
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL + '/api/studies/' + patientStudyId + '/getFullStudy';
        break;
    }

    Promise.all([
      fetch(regionurl, { headers: { Authorization: "Bearer " + token }}),
      fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/applications', { headers: { Authorization: "Bearer " + token }})
    ])
    .then(([res1, res2]) => Promise.all([manageResponse(res1), manageResponse(res2)]))
    .then(([result1json, result2json]) => {
      var study = result1json.study;
      var applications = result2json.applications || [];

      study.StudyDateFormatted = dayjs(study.StudyDate).format('l');
      study.PatientBirthDateFormatted = dayjs(study.PatientBirthDate).format('l');

      // attach app to study
      study.apps = [];
      for(var j = 0; j < applications.length; j++) {
        if(!!applications[j].studyUrl) {
          var studyUrl = applications[j].studyUrl.replace('{patientStudyId}', study.patientStudyId);
          studyUrl = studyUrl.replace('{StudyInstanceUID}', study.StudyInstanceUID);
          studyUrl = studyUrl.replace('{siteId}', study.siteId);
          studyUrl = studyUrl.replace('{AccessionNumber}', study.AccessionNumber);
          studyUrl = studyUrl.replace('{PatientID}', study.PatientID);

          var app = {
            name: applications[j].name,
            clientId: applications[j].clientId,
            studyUrl: studyUrl
          };

          study.apps.push(app);
        }
      }

      setfullstudy(study);
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [patientStudyId, token, siteRegion, ]);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/destinations?siteId=' + siteId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.destinations || [];

      setdestinations(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token, siteId]);

  const Thelist = trackWindowScroll(Gallery);

  return (
    <React.Fragment>
      <div className="row">
        <h3>Study Information</h3>
        <div className="col-md-6">
          <Table>
            <tbody>
              <tr><th>StudyInstanceUID</th><td>{fullstudy.StudyInstanceUID}</td></tr>
              <tr><th>PatientName</th><td>{fullstudy.PatientName}</td></tr>
              <tr><th>PatientID</th><td>{fullstudy.PatientID}</td></tr>
              <tr><th>Accession Number</th><td>{fullstudy.AccessionNumber}</td></tr>
              <tr><th>StudyDate</th><td>{fullstudy.StudyDateFormatted}</td></tr>
              <tr><th>StudyDescription</th><td>{fullstudy.StudyDescription}</td></tr>
              <tr><th>PatientSex</th><td>{fullstudy.PatientSex}</td></tr>
              <tr><th>PatientBirthDate</th><td>{fullstudy.PatientBirthDateFormatted}</td></tr>
              <tr><th>InstitutionName</th><td>{fullstudy.InstitutionName}</td></tr>
            </tbody>
          </Table>
          <Table>
            <thead>
              <tr>
                <th>SeriesInstanceUID</th>
                <th>Modality</th>
                <th>Series Description</th>
              </tr>
            </thead>
            <tbody>
            { fullstudy.series.map(series => { return (
              <tr key={series.SeriesInstanceUID}>
                <td>{series.SeriesInstanceUID}</td>
                <td>{series.Modality}</td>
                <td>{series.SeriesDescription}</td>
              </tr>
            )})}
            </tbody>
          </Table>
        </div>
        <div className="col-md-6">
          <h4>Send Study</h4>
            <Form>
              <Form.Group as={Row} className="mb-3" >
                <Form.Label column sm="3" className="text-end">Destinations</Form.Label>
                <Col sm={9}>
                  <Form.Select size="lg" onChange={(event) => {console.log('change ' + event.target.value);/* setselectedsiteid(event.target.value) */}}>
                  {
                    destinations.map(destination => {
                      return (
                        <option key={destination.destinationId} value={destination.destinationId}>{destination.name}</option>
                        );
                    })
                  }
                  </Form.Select>
                </Col>
              </Form.Group>
              <Form.Group as={Row} className="mb-3">
                <Col sm="3" />
                <Col sm={9}>
                  <Button variant="primary" onClick={(event) => {event.preventDefault(); /* setSite();*/}}>Send</Button>{' '}
                </Col>
              </Form.Group>
            </Form>
          <h4>Share Study</h4>
        </div>
      </div>
      <div className="row">
        <div className="col-md-6">
          <h4>Your apps</h4>
          { fullstudy.apps.map(app => {
            return <div key={app.clientId}><a href={app.studyUrl} target='_blank' rel="noreferrer">{app.name}</a></div>
          })}
        </div>
        <div className="col-md-6">
          <h4>Recommended Apps</h4>
          <div><a href={'https://espressomedical.github.io/ohif/#/site/' + siteId + '/viewer/' + fullstudy.StudyInstanceUID} target='_blank' rel="noreferrer">OHIF Viewer</a></div>
        </div>
      </div>
      <h4>Images <Button>Download</Button></h4>
      <Thelist fullstudy={fullstudy} />
    </React.Fragment>
  );
}

export default function Study() {
  let params = useParams();
  return <StudyParams patientStudyId={params.patientStudyId} />
}
