import React from 'react';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export default function SCPFields(props) {
  return (
    <React.Fragment>
    <Form.Group as={Row} className="mb-3" >
      <Form.Label column sm="3" className="text-end">Name</Form.Label>
      <Col sm={9}><Form.Control type="text" name="name" value={props.name} onChange={(event) => {props.setname(event.target.value)}} /></Col>
    </Form.Group>
    <Form.Group as={Row} className="mb-3">
      <Form.Label column sm="3" className="text-end">AE Title</Form.Label>
      <Col sm={9}><Form.Control type="text" name="aetitle" value={props.aetitle} onChange={(event) => {props.setaetitle(event.target.value)}}/></Col>
    </Form.Group>
    <Form.Group as={Row} className="mb-3">
      <Form.Label column sm="3" className="text-end">Hostname</Form.Label>
      <Col sm={9}>
        <Form.Control type="text" name="hostname" value={props.hostname} onChange={(event) => {props.sethostname(event.target.value)}}/>
        <Form.Text className="text-muted">A hostname or ip address without http://</Form.Text>
      </Col>
    </Form.Group>
    </React.Fragment>
  );
}
