import React, { useState, useCallback } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import SiteFields from './site-fields';

export default function SiteNew() {
  const [token, setToken] = useLocalStorage('token', "");
  const [name, setname] = useState("");
  const [city, setcity] = useState("");
  const [region, setregion] = useState("us");

  const navigate = useNavigate();

  const createSite = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites', { body: JSON.stringify({name, city, region}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Site created.");

      navigate('/sites')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [name, city, region, token, navigate]);

  return (
    <React.Fragment>
    <h3>Add Destination</h3>
    <Form>
      <SiteFields name={name} setname={setname} city={city} setcity={setcity} region={region} setregion={setregion} disabledRegion={false} />
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); createSite(); }}>Add</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/sites')}}>Cancel</Button>{' '}
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}
