import React, { useCallback, useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';
import socketio from 'socket.io-client';

export default function StatusList() {
  const [token, setToken] = useLocalStorage('token', "");
  const [statuslist, setstatuslist] = useState([]);
  const siteId = useSelector((state) => state.site.siteId);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/outsessions/?siteId=' + siteId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.outsessions || [];

      setstatuslist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [siteId, token]);

  useEffect(() => {
    const socket = new socketio(process.env.REACT_APP_SOCKETIO_URL, { transports: ['websocket'] });

    socket.on('updateoutsessionitem', (data) => {
      var found = statuslist.findIndex((element, index, array) => { return (data.outSessionId === element.outSessionId) });
      if(found !== -1) {
        statuslist[found] = data.outsessionitem;
        setstatuslist(statuslist);
      }
      else {
        setstatuslist([data, ...statuslist]);
      }
    });
  })

  const cancelhandler = useCallback((outSessionId) => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/outsessions/' + outSessionId + '/cancel', { headers: { Authorization: "Bearer " + token }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .catch(function(err) {
      toast.error(err);
    })
  }, [token]);

  return (
    <React.Fragment>
    <h3>Status</h3>
    <Table bordered>
      <thead>
        <tr>
          <th>Study</th>
          <th>PatientID</th>
          <th>Patient Name</th>
          <th>Study Date</th>
          <th title='Modalities'>Mod</th>
          <th>scp</th>
          <th>Destination</th>
          <th>last update</th>
          <th>started at</th>
          <th>status</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      {
        statuslist.map(item => {
          return (
            <tr key={item.outSessionId}>
              <td><Link to={'/studies/' + item.patientStudyId}>Study</Link></td>
              <td>{item.PatientID}</td>
              <td>{item.PatientName}</td>
              <td>{new Date(item.StudyDate).toLocaleDateString()}</td>
              <td>{item.ModalitiesInStudy}</td>
              <td><Link to={'/scps/' + item.scpId}>{item.scpName}</Link></td>
              <td><Link to={'/destinations/' + item.destinationId}>{item.destinationName}</Link></td>
              <td><div className='devinfo'>{item.outSessionId}</div>{new Date(item.updatedAt).toLocaleDateString()}</td>
              <td><div className='devinfo'>{item.createdAt}</div>{new Date(item.createdAt).toLocaleDateString()}</td>
              <td>{item.status}</td>
              <td><Button variant="outline-secondary" onClick={() => cancelhandler(item.outSessionId)}>Cancel</Button></td>
            </tr>
          );
        })
      }
      </tbody>

    </Table>
    </React.Fragment>
  );
};
