import React, { useEffect, useState, useCallback } from 'react';
import { useParams } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { setCurrentSite } from '../slices/currentsite';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import SiteFields from './site-fields';

function SiteParams({siteId}) {
  const [token, setToken] = useLocalStorage('token', "");
  const [name, setname] = useState("");
  const [city, setcity] = useState("");
  const [region, setregion] = useState("");
  const currentsiteid = useSelector((state) => state.site.siteId);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites/' + siteId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      setname(response.site.name);
      setcity(response.site.city);
      setregion(response.site.region);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [siteId, token]);

  const navigate = useNavigate();

  const updateSite = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites/' + siteId, { body: JSON.stringify({siteId, name, city}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Site updated.");

      navigate('/sites')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [siteId, name, city, token, navigate]);


  const deleteSite = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites/' + siteId, { headers: { Authorization: "Bearer " + token }, method: 'DELETE'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Site deleted.");

      if(siteId === currentsiteid) {
        setCurrentSite(null);
      }

      navigate('/sites')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [siteId, token, navigate, currentsiteid]);

  return (
    <React.Fragment>
    <h3>Site</h3>
    <Form>
      <SiteFields name={name} setname={setname} city={city} setcity={setcity} region={region} setregion={setregion} disabledRegion={true} />
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); updateSite(); }}>Update</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/sites')}}>Cancel</Button>{' '}
          <Button variant="outline-danger" onClick={(event) => {event.preventDefault();deleteSite()}}>Delete</Button>
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}

export default function Site() {
  let params = useParams();
  return <SiteParams siteId={params.siteId} />
}
