import React, { useCallback, useState, useEffect } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Table from 'react-bootstrap/Table';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import { toast } from 'react-toastify';
import manageResponse from '../manageResponse';

export default function SiteUsers() {
  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);
  const siteId = useSelector((state) => state.site.siteId);
  const siteName = useSelector((state) => state.site.name);
  const [email, setemail] = useState("");

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites/' + siteId + '/users', { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.siteusers || [];

      setresultlist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })
  } /* always run on state change*/);

  const changeadmin = useCallback((userId, isAdmin) => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites/' + siteId + '/users/' + userId + (isAdmin ? '/makeAdmin' : '/removeAdmin'), { headers: { Authorization: "Bearer " + token }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success(userId + (isAdmin? ' is now an Admin' : ' is no longer an Admin'));
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [siteId, token]);

  const removeuser = useCallback((userId) => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites/' + siteId + '/users/' + userId, { headers: { Authorization: "Bearer " + token }, method: 'DELETE'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success(userId + ' removed.');
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [siteId, token]);

  const adduser = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/invitations/emails', { body: JSON.stringify({siteId, email}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success(email + ' added.');
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [email, siteId, token]);

  return (
    <React.Fragment>
    <h3>Site Users for {siteName}</h3>
    <Table striped bordered>
      <thead>
        <tr>
          <th>userId</th>
          <th>Roles</th>
          <th>Admin</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      {
        resultlist.map(user => {
          return (
            <tr key={user.userId}>
              <td>{user.userId}</td>
              <td>{ Object.keys(user.roles).map(key => { return (<span key={key}>{key}</span>)})}</td>
              <td><Form.Check onChange={(event) => changeadmin(user.userId, event.target.checked)} checked={ user.roles.admin ? true : false}/></td>
              <td><Button variant="outline-danger" onClick={() => removeuser(user.userId)}>Remove</Button></td>
            </tr>
          );
        })
      }
      </tbody>
    </Table>
    <h3>Invite via Emails</h3>
    <Form>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Email</Form.Label>
        <Col sm={9}><Form.Control type="text" name="email" value={email} onChange={(event) => {setemail(event.target.value)}} /></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault();adduser(); }}>Inviate to site</Button>
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}
