import React from 'react';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export default function WebhooksFields(props) {
  return (
    <React.Fragment>
    <Form.Group as={Row} className="mb-3">
      <Form.Label column sm="3" className="text-end">Active</Form.Label>
      <Col sm={9}>
        <Form.Check onChange={(event) => props.setactive(event.target.checked ? true : false)} checked={props.active}/>
        <Form.Text className="text-muted">Enable and disable webhook</Form.Text>
      </Col>
    </Form.Group>
    <Form.Group as={Row} className="mb-3" >
      <Form.Label column sm="3" className="text-end">Url</Form.Label>
      <Col sm={9}><Form.Control type="text" name="name" value={props.url} onChange={(event) => {props.seturl(event.target.value)}} /></Col>
    </Form.Group>
    <Form.Group as={Row} className="mb-3">
      <Form.Label column sm="3" className="text-end">Provide PHI</Form.Label>
      <Col sm={9}>
        <Form.Check onChange={(event) => props.setprovidePHI(event.target.checked ? true : false)} checked={props.providePHI}/>
        <Form.Text className="text-muted">Only enable if the url service is HIPAA compliant.  If disabled, the hook is not provided with patientName and patientID</Form.Text>
      </Col>
    </Form.Group>
    <Form.Group as={Row} className="mb-3">
      <Form.Label column sm="3" className="text-end">Verify Certificate</Form.Label>
      <Col sm={9}>
        <Form.Check onChange={(event) => props.setverifyCert(event.target.checked ? true : false)} checked={props.verifyCert}/>
        <Form.Text className="text-muted">Verify certificate domain matches. Required for production use.</Form.Text>
      </Col>
    </Form.Group>    
    </React.Fragment>
  );
}
