import { createStore, combineReducers, applyMiddleware, compose } from 'redux'
import auth from './slices/auth'
import site from './slices/currentsite'

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

// convert object to string and store in sessionStorage
function saveToSessionStorage(state) {
  try {
    const serialisedState = JSON.stringify(state);
    sessionStorage.setItem("persistantState", serialisedState);
  } catch (e) {
    console.warn(e);
  }
}

// load string from localStarage and convert into an Object
// invalid output must be undefined
function loadFromSessionStorage() {
  try {
    const serialisedState = sessionStorage.getItem("persistantState");
    if (serialisedState === null) return undefined;
    return JSON.parse(serialisedState);
  } catch (e) {
    console.warn(e);
    return undefined;
  }
}

const configureStore = (initialState) => {

  const store = createStore(
    combineReducers({
      // other reducers
      auth,
      site,
    }),
    loadFromSessionStorage(),
    composeEnhancers(
    applyMiddleware(
      // other middleware
    ))
  )

  store.subscribe(() => saveToSessionStorage(store.getState()));

  return store;
}

export default configureStore
