import React, { useEffect, useState, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useLocalStorage } from '../useLocalStorage';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import { setCurrentSite } from '../slices/currentsite';
import { useNavigate, useLocation } from "react-router-dom";
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';

export default function SwitchSite() {

  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);
  const currentsiteid = useSelector((state) => state.site.siteId);
  const [selectedsiteid, setselectedsiteid] = useState(currentsiteid);

  const dispatch = useDispatch();

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites', { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.sites || [];

      setresultlist(result);
      if(!selectedsiteid)
        setselectedsiteid(result[0].siteId);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [selectedsiteid, token]);

  const navigate = useNavigate();
  const location = useLocation();

  const setSite = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/sites/' + selectedsiteid, { headers: { Authorization: "Bearer " + token }})
    .then((response) => {
      return response.json();
    })
    .then((response) => {
      dispatch(setCurrentSite(response.site));
      if(location.state?.from) {
        console.log('going to ' + location.state.from.pathname)
        navigate(location.state.from);
      }
      else {
        navigate(-1);
      }
    })
    .catch(function(err) {
      toast.error(err);
    });
  },
    [dispatch, navigate, selectedsiteid, token, location.state?.from]
  )

  return (
    <React.Fragment>
    <h3>Swtich Site</h3>
    <Form>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Site</Form.Label>
        <Col sm={9}>
          <Form.Select size="lg" value={currentsiteid} onChange={(event) => {console.log('change ' + event.target.value);setselectedsiteid(event.target.value)}}>
          {
            resultlist.map(site => {
              return (
                <option key={site.siteId} value={site.siteId}>{site.name + ' (' + site.siteId + ')'}</option>
                );
            })
          }
          </Form.Select>
        </Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); setSite();}}>Switch</Button>{' '}
          <Button variant="outline-secondary" onClick={() => navigate(-1)}>Cancel</Button>
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}
