import React, { useState, useCallback } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import SCPFields from './scp-fields';

export default function SCPNew() {
  const [token, setToken] = useLocalStorage('token', "");
  const siteId = useSelector((state) => state.site.siteId);
  const [name, setname] = useState("");
  const [aetitle, setaetitle] = useState("");
  const [hostname, sethostname] = useState("");

  const navigate = useNavigate();

  const createSCP = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/scps', { body: JSON.stringify({siteId, name, aetitle, hostname}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("SCP created.");

      navigate('/scps')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [siteId, name, aetitle, hostname, token, navigate]);

  return (
    <React.Fragment>
    <h3>Add Destination</h3>
    <Form>
      <SCPFields name={name} setname={setname} aetitle={aetitle} setaetitle={setaetitle} hostname={hostname} sethostname={sethostname} />
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); createSCP(); }}>Add</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/scps')}}>Cancel</Button>{' '}
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}
