import React, { useState, useCallback } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import ClientFields from './client-fields';

export default function ClientNew() {
  const [token, setToken] = useLocalStorage('token', "");
  const [name, setname] = useState("");
  const [homepage, sethomepage] = useState("");
  const [description, setdescription] = useState("");
  const [clientType, setclientType] = useState(0);
  const [callbackurl, setcallbackurl] = useState("");
  const [organization, setorganization] = useState("");
  const [organizationHomepage, setorganizationHomepage] = useState("");
  const [studyUrl, setstudyUrl] = useState("");
  const [installationUrl, setinstallationUrl] = useState("");
  const [webhookUrl, setwebhookUrl] = useState("");

  const navigate = useNavigate();

  const createClient = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/clients', { body: JSON.stringify({name, homepage, description, clientType, callbackurl, organization, organizationHomepage, studyUrl, installationUrl, webhookUrl}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Client created.");

      navigate('/developer/clients')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [name, homepage, description, clientType, callbackurl, organization, organizationHomepage, studyUrl, installationUrl, webhookUrl, token, navigate]);

  return (
    <React.Fragment>
    <h3>Add Client</h3>
    <Form>
      <ClientFields name={name} setname={setname} homepage={homepage} sethomepage={sethomepage} description={description}
        setdescription={setdescription} clientType={clientType} setclientType={setclientType} callbackurl={callbackurl}
        setcallbackurl={setcallbackurl} organization={organization} setorganization={setorganization}
        organizationHomepage={organizationHomepage} setorganizationHomepage={setorganizationHomepage} studyUrl={studyUrl} setstudyUrl={setstudyUrl}
        installationUrl={installationUrl} setinstallationUrl={setinstallationUrl} webhookUrl={webhookUrl} setwebhookUrl={setwebhookUrl} />
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); createClient(); }}>Add</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/developer/clients')}}>Cancel</Button>{' '}
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}
