import React, { useState, useEffect } from 'react';
import { useLocalStorage } from './useLocalStorage';
import { useSelector, useDispatch } from 'react-redux';
import { Link, useNavigate } from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import ErrorBoundary from './errorboundary';

export default function LoggedInLayout({ children }) {
  const currentsitename = useSelector((state) => state.site.name);
  const [currentuser, setcurrentuser] = useState("");
  const [token, setToken] = useLocalStorage('token', "");
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    if(token) {
      fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/users', { headers: { Authorization: "Bearer " + token }})
      .then((res1) => res1.json())
      .then((response) => setcurrentuser(response.user.name))
    }
    else {
      setcurrentuser("");
    }
  }, [token]);

  return (
    <React.Fragment>
      <style type="text/css">
        {`
    .mainstuff {
      background-color: white;
    }
    `}
      </style>
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Container fluid>
          <Navbar.Brand as={Link} to="/">Espresso</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav>
              <Nav.Link as={Link} to="/">Search PACS</Nav.Link>
              <Nav.Link as={Link} to='/statuslist'>Status List</Nav.Link>
              <NavDropdown title="Settings">
                <NavDropdown.Item as={Link} to="/destinations">Destinations</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/siteusers">Site Users</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/scps">SCPs</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/webhooks">Webhooks</NavDropdown.Item>
              </NavDropdown>
              <Nav.Link as={Link} to='/marketplace'>Marketplace</Nav.Link>
              <Nav.Link as={Link} to="/switchsite">{currentsitename}</Nav.Link>
            </Nav>
            <Nav className="ms-auto">
              <NavDropdown title={currentuser} align="end">
                <NavDropdown.Item as={Link} to="/switchsite">Switch Site</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/user/profile">Profile</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/sites">Sites</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/applications">Authorized Applications</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/developer">Developer</NavDropdown.Item>
                <NavDropdown.Item onClick={() => {
                  setToken("");
                  window.history.push("/");
                }}>Logout</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item as={Link} to="/globaladmin">Global Admin</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <ErrorBoundary>
        <Container fluid className="mt-4 mainstuff">{children}</Container>
      </ErrorBoundary>
      <Container fluid>
        <div><p><a href='/contact'>Contact Us</a> <a href="/developer">Developer</a></p></div>
      </Container>
    </React.Fragment>
  );
}
