import React, { useState, useCallback } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import WebhookFields from './webhook-fields';

export default function WebhookNew() {
  const [token, setToken] = useLocalStorage('token', "");
  const siteId = useSelector((state) => state.site.siteId);
  const [active, setactive] = useState(true);
  const [url, seturl] = useState("");
  const [providePHI, setprovidePHI] = useState(false);
  const [verifyCert, setverifyCert] = useState(false);

  const navigate = useNavigate();

  const createWebhook = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/webhooks', { body: JSON.stringify({siteId, active, url, providePHI, verifyCert}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Webhook created.");

      navigate('/webhooks')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [siteId, active, url, providePHI, verifyCert, token, navigate]);

  return (
    <React.Fragment>
    <h3>Add Webhook</h3>
    <Form>
      <WebhookFields active={active} setactive={setactive} url={url} seturl={seturl} providePHI={providePHI} setprovidePHI={setprovidePHI} verifyCert={verifyCert} setverifyCert={setverifyCert} />
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); createWebhook(); }}>Add</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/webhooks')}}>Cancel</Button>{' '}
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}
