import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';

import * as dayjs from 'dayjs'
var relativeTime = require('dayjs/plugin/relativeTime')
dayjs.extend(relativeTime)

export default function Webhooks() {
  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);
  const siteId = useSelector((state) => state.site.siteId);
  const siteName = useSelector((state) => state.site.name);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/webhooks?siteId=' + siteId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.webhooks || [];

      setresultlist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token, siteId]);

  return (
    <React.Fragment>
    <h3>Webhooks for site: {siteName}</h3>
    <Table striped bordered>
      <thead>
        <tr>
          <th>Active</th>
          <th>Url</th>
          <th>Provide PHI</th>
          <th>Verify Certificate</th>
          <th>clientId</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      { resultlist.map(webhook => { return (
        <tr key={webhook.webhookId}>
          <td><Form.Check disabled checked={ webhook.active ? true : false}/></td>
          <td>{webhook.url}</td>
          <td><Form.Check disabled checked={ webhook.providePHI ? true : false}/></td>
          <td><Form.Check disabled checked={ webhook.verifyCert ? true : false}/></td>
          <td>{ webhook.clientId && <Button variant="outline-secondary" as={Link} to={'/marketplace/' + webhook.clientId}>{webhook.clientId}</Button>}</td>
          <th><Button variant="outline-secondary" as={Link} to={'/webhooks/' + webhook.webhookId}>Edit</Button></th>
        </tr>
      )})}
      </tbody>
    </Table>
    <Button as={Link} to='/webhooks/new'>New</Button>
    <p />
    <h3>Web hook events</h3>
    <p>Hooks will be called as a HTTP POST with a JSON object.</p>
    <h4>Instance Added</h4>
    <p>A JSON object, with event set to 'instance_added', and instance set to the instance data. If For example.</p>
<pre>{
`{
  event: 'instance_added',
  instance: {
    siteId,
    patientStudyId,
    seriesId,
    instanceId,
    StudyInstanceUID,
    SeriesInstanceUID,
    SOPInstanceUID,
    SOPClassUID,
    MD5,
    PatientName,
    PatientID,
    ...
  }
}`}
</pre>
  <h5>Instance Modified</h5>
  <p>A JSON object, with event set to 'instance_modified', and instance set to the instance data. For example.</p>
<pre>{
`{
  event: 'instance_modified',
  instance: {
    siteId,
    patientStudyId,
    seriesId,
    instanceId,
    StudyInstanceUID,
    SeriesInstanceUID,
    SOPInstanceUID,
    SOPClassUID,
    MD5,
    PatientName,
    PatientID,
    ...
    }
  }
`}
</pre>
    </React.Fragment>
  );
}
