import React, { useEffect, useState, useCallback } from 'react';
import { useParams } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import SCPFields from './scp-fields';

function SCPParams({scpId}) {
  const [token, setToken] = useLocalStorage('token', "");
  const [name, setname] = useState("");
  const [aetitle, setaetitle] = useState("");
  const [hostname, sethostname] = useState("");

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/scps/' + scpId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      setname(response.scp.name);
      setaetitle(response.scp.aetitle);
      sethostname(response.scp.hostname);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [scpId, token]);

  const navigate = useNavigate();

  const updateSCP = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/scps/' + scpId, { body: JSON.stringify({scpId, name, aetitle, hostname}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("SCP updated.");

      navigate('/scps')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [scpId, name, aetitle, hostname, token, navigate]);


  const deleteSCP = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/scps/' + scpId, { headers: { Authorization: "Bearer " + token }, method: 'DELETE'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("SCP deleted.");

      navigate('/scps')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [scpId, token, navigate]);

  return (
    <React.Fragment>
    <h3>SCP</h3>
    <Form>
      <SCPFields name={name} setname={setname} aetitle={aetitle} setaetitle={setaetitle} hostname={hostname} sethostname={sethostname} />
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); updateSCP(); }}>Update</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/scps')}}>Cancel</Button>{' '}
          <Button variant="outline-danger" onClick={(event) => {event.preventDefault();deleteSCP()}}>Delete</Button>
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}

export default function SCP() {
  let params = useParams();
  return <SCPParams scpId={params.scpId} />
}
