import React, { useState, useCallback } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import DestinationFields from './destination-fields';

export default function DestinationNew() {
  const [token, setToken] = useLocalStorage('token', "");
  const [name, setname] = useState("");
  const [scpId, setscpId] = useState("");
  const [destinationHost, setdestinationHost] = useState("");
  const [destinationPort, setdestinationPort] = useState("");
  const [destinationAE, setdestinationAE] = useState("");
  const [sourceAE, setsourceAE] = useState("");
  const [presentationContext, setpresentationContext] = useState("");

  const navigate = useNavigate();

  const createDestination = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/destinations', { body: JSON.stringify({name, scpId, destinationHost, destinationPort, destinationAE, sourceAE, presentationContext}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Destination created.");

      navigate('/destinations')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [name, scpId, destinationHost, destinationPort, destinationAE, sourceAE, presentationContext, token, navigate]);

  return (
    <React.Fragment>
    <h3>Add Destination</h3>
    <Form>
      <DestinationFields name={name} setname={setname} scpId={scpId} setscpId={setscpId} destinationHost={destinationHost} setdestinationHost={setdestinationHost} destinationPort={destinationPort} setdestinationPort={setdestinationPort} destinationAE={destinationAE} setdestinationAE={setdestinationAE} sourceAE={sourceAE} setsourceAE={setsourceAE} presentationContext={presentationContext} setpresentationContext={setpresentationContext} />
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); createDestination(); }}>Add</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/destinations')}}>Cancel</Button>{' '}
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}
