import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';

export default function Destinations() {
  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);
  const siteId = useSelector((state) => state.site.siteId);
  const siteName = useSelector((state) => state.site.name);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/destinations?siteId=' + siteId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.destinations || [];

      setresultlist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token, siteId]);

  return (
    <React.Fragment>
    <h3>DICOM Destinations for {siteName}</h3>
    <Table striped bordered>
      <thead>
        <tr>
          <th>Name</th>
          <th>Scp</th>
          <th>Destination Host</th>
          <th>Destination Port</th>
          <th>Destination AE</th>
          <th>Source AE</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      { resultlist.map(destination => { return (
        <tr key={destination.destinationId}>
          <td>{destination.name}</td>
          <td><Link to={'/scps/' + destination.scpId}>{destination.scpName}</Link></td>
          <td>{destination.destinationHost}</td>
          <td>{destination.destinationPort}</td>
          <td>{destination.destinationAE}</td>
          <td>{destination.sourceAE}</td>
          <th><Button variant="outline-secondary" as={Link} to={'/destinations/' + destination.destinationId}>Edit</Button></th>
        </tr>
      )})}
      </tbody>
    </Table>
    <Button as={Link} to='/destinations/new'>New</Button>
    </React.Fragment>
  );
}
