import React, { useEffect, useState } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';

export default function DestinationFields(props) {
  const [token, setToken] = useLocalStorage('token', "");
  const siteId = useSelector((state) => state.site.siteId);
  const [scplist, setscplist] = useState([]);
  const {scpId, setscpId} = props;
  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/scps?siteId=' + siteId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.scps || [];

      setscplist(result);
      if(!scpId)
        setscpId(result[0].scpId);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token, siteId, scpId, setscpId]);

  return (
    <React.Fragment>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Name</Form.Label>
        <Col sm={9}><Form.Control type="text" name="name" value={props.name} onChange={(event) => {props.setname(event.target.value)}} /></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">SCP</Form.Label>
        <Col sm={9}><Form.Select size="lg" value={props.scpId} onChange={(event) => {props.setscpId(event.target.value)}}>
        {
          scplist.map(scp => {
            return (
              <option key={scp.scpId} value={scp.scpId}>{scp.name + ' (' + scp.scpId + ')'}</option>
              );
          })
        }
        </Form.Select></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Destination Host</Form.Label>
        <Col sm={9}><Form.Control type="text" name="destinationHost" value={props.destinationHost} onChange={(event) => {props.setdestinationHost(event.target.value)}} /></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Destination Port</Form.Label>
        <Col sm={9}><Form.Control type="text" name="destinationPort" value={props.destinationPort} onChange={(event) => {props.setdestinationPort(event.target.value)}} /></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Destination AE</Form.Label>
        <Col sm={9}><Form.Control type="text" name="destinationAE" value={props.destinationAE} onChange={(event) => {props.setdestinationAE(event.target.value)}} /></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Source AE</Form.Label>
        <Col sm={9}><Form.Control type="text" name="sourceAE" value={props.sourceAE} onChange={(event) => {props.setsourceAE(event.target.value)}} /></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Presentation Contexts</Form.Label>
        <Col sm={9}>
          <Form.Control as="textarea" rows={5} name="presentationContext" value={props.presentationContext} onChange={(event) => {props.setpresentationContext(event.target.value)}} />
          <Form.Text className="text-muted">
            <p>{`JSON object { "classUid": [ "transferSyntax", "transferSyntax", ... ], "classUid": [ "transferSyntax", ... ] }`}</p>
            <p>references: <a href="https://www.dicomlibrary.com/dicom/sop/" target='_blank' rel="noreferrer">SOP classes</a> and <a href="https://www.dicomlibrary.com/dicom/transfer-syntax/" target='_blank' rel="noreferrer">Transfer syntax</a></p>
            <p>For example the following json will limit "Ultrasound Image Storage (1.2.840.10008.5.1.4.1.1.6.1)" to only "Explicit VR Little Endian (1.2.840.10008.1.2.1)", and limit "MR Image Storage (1.2.840.10008.5.1.4.1.1.4)" to "JPEG Baseline (1.2.840.10008.1.2.4.50)" and "Explicit VR Little Endian (1.2.840.10008.1.2.1)"</p>
            <pre>
  {`{
  "1.2.840.10008.5.1.4.1.1.6.1": [ "1.2.840.10008.1.2.1" ],
  "1.2.840.10008.5.1.4.1.1.4": [ "1.2.840.10008.1.2.4.50",  "1.2.840.10008.1.2.1" ]
}`}</pre>
          </Form.Text>
        </Col>
      </Form.Group>
    </React.Fragment>
  )
}
