import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';

import * as dayjs from 'dayjs'
var relativeTime = require('dayjs/plugin/relativeTime')
dayjs.extend(relativeTime)

export default function SCPs() {
  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);
  const siteId = useSelector((state) => state.site.siteId);
  const siteName = useSelector((state) => state.site.name);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/scps?siteId=' + siteId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.scps || [];

      setresultlist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token, siteId]);

  return (
    <React.Fragment>
    <h3>DICOM SCPs for site: {siteName}</h3>
    <Table striped bordered>
      <thead>
        <tr>
          <th>Name</th>
          <th>AE Title</th>
          <th>Hostname</th>
          <th>Version</th>
          <th>lastPing</th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      { resultlist.map(scp => { return (
        <tr key={scp.scpId}>
          <td>{scp.name}</td>
          <td>{scp.aetitle}</td>
          <td>{scp.hostname}</td>
          <td>{scp.version}</td>
          <td>{dayjs(scp.lastPing).isValid() ? (dayjs(scp.lastPing).startOf('minute').fromNow() + ' ('+ scp.lastPing + ')') : 'never'}</td>
          <th><Button variant="outline-secondary" as={Link} to={'/scps/' + scp.scpId}>Edit</Button></th>
          <th><a href={scp.hostname}>Manage</a></th>
        </tr>
      )})}
      </tbody>
    </Table>
    <Button as={Link} to='/scps/new'>New</Button>
    </React.Fragment>
  );
}
