import { createSlice } from '@reduxjs/toolkit'

const initialState = { }

const currentsiteSlice = createSlice({
  name: 'site',
  initialState,
  reducers: {
    setCurrentSite(state, action) {
      state.siteId = action.payload.siteId;
      state.name = action.payload.name;
    },
    clearCurrentSite(state) {
      state = null;
    },
  },
})

export const { setCurrentSite, clearCurrentSite } = currentsiteSlice.actions
export default currentsiteSlice.reducer
