import React, { useEffect, useState, useCallback } from 'react';
import { useParams, Link } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import InputGroup from 'react-bootstrap/InputGroup';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import WebhookFields from './webhook-fields';

function WebhookParams({webhookId}) {
  const [token, setToken] = useLocalStorage('token', "");
  const [active, setactive] = useState(true);
  const [url, seturl] = useState("");
  const [providePHI, setprovidePHI] = useState(false);
  const [verifyCert, setverifyCert] = useState(true);
  const [clientId, setclientId] = useState();

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/webhooks/' + webhookId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      setactive(response.webhook.active);
      seturl(response.webhook.url);
      setprovidePHI(response.webhook.providePHI);
      setverifyCert(response.webhook.verifyCert);
      setclientId(response.webhook.clientId);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [webhookId, token]);

  const navigate = useNavigate();

  const updateWebhook = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/webhooks/' + webhookId, { body: JSON.stringify({webhookId, active, url, providePHI, verifyCert}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Webhook updated.");

      navigate('/webhooks')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [webhookId, active, url, providePHI, verifyCert, token, navigate]);


  const deleteWebhook = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/webhooks/' + webhookId, { headers: { Authorization: "Bearer " + token }, method: 'DELETE'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Webhook deleted.");

      navigate('/webhooks')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [webhookId, token, navigate]);

  return (
    <React.Fragment>
    <h3>Webhook</h3>
    <Form>
      <WebhookFields active={active} setactive={setactive} url={url} seturl={seturl} providePHI={providePHI} setprovidePHI={setprovidePHI} verifyCert={verifyCert} setverifyCert={setverifyCert} />
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">App</Form.Label>
        <Col sm={9}>{ clientId && <InputGroup><Button as={Link} to={'/marketplace/' + clientId}>App Detail</Button><Form.Control type="text" value={clientId} disabled/></InputGroup>}</Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); updateWebhook(); }}>Update</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/webhooks')}}>Cancel</Button>{' '}
          <Button variant="outline-danger" onClick={(event) => {event.preventDefault();deleteWebhook()}}>Delete</Button>
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}

export default function Webhook() {
  let params = useParams();
  return <WebhookParams webhookId={params.webhookId} />
}
