import React, { useCallback, useState, useMemo } from 'react';
import { Link } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';
import manageResponse from '../manageResponse';
import { useSessionStorage } from '../useSessionStorage';
import { toast } from 'react-toastify';
import { SortDown, SortDownAlt, ArrowDownUp } from 'react-bootstrap-icons';
import * as dayjs from 'dayjs';
var localizedFormat = require('dayjs/plugin/localizedFormat')
dayjs.extend(localizedFormat)

function SearchResultList(props) {
  const { data } = props;
  const [sortConfig, setSortConfig] = useState(null);

  const sortedItems = useMemo(() => {
    let sortableItems = [...data];
    if (sortConfig !== null) {
      sortableItems.sort((a, b) => {
        if (a[sortConfig.key] < b[sortConfig.key]) {
          return sortConfig.direction === 'ascending' ? -1 : 1;
        }
        if (a[sortConfig.key] > b[sortConfig.key]) {
          return sortConfig.direction === 'ascending' ? 1 : -1;
        }
        return 0;
      });
    }
    return sortableItems;
  }, [data, sortConfig]);

  const getSortIcon = useCallback((key) => {
    if(sortConfig?.key === key) {
      if(sortConfig.direction === 'ascending')
        return <SortDownAlt />
      else
        return <SortDown />
    } else {
      return <ArrowDownUp />
    }
  }, [sortConfig]);

  const sortIconPatientName = useMemo(() => {
    return getSortIcon('PatientName');
  }, [getSortIcon]);

  const sortIconPatientID = useMemo(() => {
    return getSortIcon('PatientID');
  }, [getSortIcon]);

  const sortIconStudyDate = useMemo(() => {
    return getSortIcon('StudyDate');
  }, [getSortIcon]);

  const requestSort = key => {
    let direction = 'ascending';
    if (sortConfig && sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending';
    }
    setSortConfig({ key, direction });
  }

  return (
      <div>
        <Table striped bordered>
          <thead>
            <tr>
              <th></th>
              <th>Tools</th>
              <th>Name<span className="right" onClick={() => requestSort('PatientName')}>{sortIconPatientName}</span></th>
              <th>PatientID<span className="right" onClick={() => requestSort('PatientID')}>{sortIconPatientID}</span></th>
              <th>StudyDate<span className="right" onClick={() => requestSort('StudyDate')}>{sortIconStudyDate}</span></th>
              <th>Mod</th>
              <th>Description</th>
              <th>Sex</th>
              <th>Birthdate</th>
              <th>InstitutionName</th>
            </tr>
          </thead>
          <tbody>
          { sortedItems.map(item => { return (
            <tr key={item.patientStudyId}>
              <td><Link to={'/studies/' + item.patientStudyId}>View</Link></td>
              <td>
                { item.apps.map(app => {
                  return <div key={app.clientId}><a href={app.studyUrl} target='_blank' rel="noreferrer" >{app.name}</a></div>
                })}
              </td>
              <td>{item.PatientName}</td>
              <td>{item.PatientID}</td>
              <td>{item.StudyDateFormatted}</td>
              <td>{item.ModalitiesInStudy}</td>
              <td>{item.StudyDescription}</td>
              <td>{item.PatientSex}</td>
              <td>{item.PatientBirthDateFormatted}</td>
              <td>{item.InstitutionName}</td>
            </tr>
          )})}
          </tbody>
        </Table>
      </div>

  )
}

export default function SearchPage() {
  const [searchresultlist, setsearchresultlist] = useSessionStorage('searchresultlist', []);
  const [token, setToken] = useLocalStorage('token', "");
  const siteId = useSelector((state) => state.site.siteId);
  const siteName = useSelector((state) => state.site.name);
  const siteRegion = useSelector((state) => state.site.region);
  const [patientname, setpatientname] = useSessionStorage('patientname', "");
  const [patientid, setpatientid] = useSessionStorage('patientid', "");
  const [studydate, setstudydate] = useSessionStorage('studydate', "");
  const [studyinstanceuid, setstudyinstanceuid] = useSessionStorage('studyinstanceuid', "");

  const search = useCallback(() => {
    var querystring = "";
    if(patientname)
      querystring += 'PatientName=' + patientname;

    if(patientid) {
      if(querystring.length > 0)  querystring += '&';
      querystring += 'PatientID=' + patientid;
    }

    if(studydate) {
      if(querystring.length > 0)  querystring += '&';
      querystring += 'StudyDate=' + studydate;
    }
    if(studyinstanceuid) {
      // overide any previous query
      querystring = 'StudyInstanceUID=' + studyinstanceuid;
    }

    var regionurl = null;
    switch(siteRegion) {
      case 'us':
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL + '/api/studies/search?siteId=' + siteId + '&' + querystring;
        break;
      case 'tw':
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL_TW + '/api/studies/search?siteId=' + siteId + '&' + querystring;
        break;
      default:
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL + '/api/studies/search?siteId=' + siteId + '&' + querystring;
        break;
    }

    Promise.all([
      fetch(regionurl, { headers: { Authorization: "Bearer " + token }}),
      fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/applications', { headers: { Authorization: "Bearer " + token }})
    ])
    .then(([res1, res2]) => Promise.all([manageResponse(res1), manageResponse(res2)]))
    .then(([result1json, result2json]) => {
      var studies = result1json.studies || [];
      var applications = result2json.applications || [];
      for (var index = 0;index < studies.length; index++) {
        var item = studies[index];
        item.StudyDateFormatted = dayjs(item.StudyDate).format('l');
        item.PatientBirthDateFormatted = dayjs(item.PatientBirthDate).format('l');
      }

      // attach app to studies
      for(var i = 0; i < studies.length; i++) {
        studies[i].apps = [];
        for(var j = 0; j < applications.length; j++) {
          if(!!applications[j].studyUrl) {
            var studyUrl = applications[j].studyUrl.replace('{patientStudyId}', studies[i].patientStudyId);
            studyUrl = studyUrl.replace('{StudyInstanceUID}', studies[i].StudyInstanceUID);
            studyUrl = studyUrl.replace('{siteId}', studies[i].siteId);
            studyUrl = studyUrl.replace('{AccessionNumber}', studies[i].AccessionNumber);
            studyUrl = studyUrl.replace('{PatientID}', studies[i].PatientID);

            var app = {
              name: applications[j].name,
              clientId: applications[j].clientId,
              studyUrl: studyUrl
            };

            studies[i].apps.push(app);
          }
        }
      }

      setsearchresultlist(studies);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [setsearchresultlist, patientname, patientid, studydate, studyinstanceuid, siteId, siteRegion, token]);

  return (
    <React.Fragment>
    <div className="row">
      <h3>Search {siteName}</h3>
      <div className="col-md-6">
        <Form>
          <Form.Group as={Row} className="mb-3" >
            <Form.Label column sm="3" className="text-end">Name</Form.Label>
            <Col sm={9}><Form.Control type="text" name="patientname" value={patientname} onChange={(event) => {setpatientname(event.target.value)}} /></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm="3" className="text-end">ID</Form.Label>
            <Col sm={9}><Form.Control type="text" name="patientid" value={patientid} onChange={(event) => {setpatientid(event.target.value)}}/></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm="3" className="text-end">Date</Form.Label>
            <Col sm={9}><Form.Control type="date" name="studydate" value={studydate} onChange={(event) => {setstudydate(event.target.value)}}/></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Form.Label column sm="3" className="text-end">StudyUID</Form.Label>
            <Col sm={9}><Form.Control type="text" name="studyinstanceuid" value={studyinstanceuid} onChange={(event) => {setstudyinstanceuid(event.target.value)}} /></Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Col sm="3" />
            <Col sm={9}>
              <Button variant="primary" onClick={(event) => {event.preventDefault(); search(); }}>Search</Button>{' '}
              <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();setpatientname("");setpatientid(""); setstudydate(""); setstudyinstanceuid("");setsearchresultlist([])}}>Clear</Button>
            </Col>
          </Form.Group>
        </Form>
      </div>
      <div className="col-md-3">
        <Button variant="primary" as={Link} to="/studies/upload">Upload</Button>
      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <SearchResultList data={searchresultlist}/>
      </div>
    </div>
    </React.Fragment>
  );
}
