import React, { useEffect, useState } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { Link } from "react-router-dom";
import { toast } from 'react-toastify';

export default function Clients() {

  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/clients', { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.clients || [];

      setresultlist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token]);

  return (
    <React.Fragment>
    <h3>Clients</h3>
    <Table striped bordered>
      <thead>
        <tr>
          <th>Name</th>
          <th>Description</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      { resultlist.map(client => { return (
        <tr key={client.clientId}>
          <td>{client.name}</td>
          <td>{client.description}</td>
          <td><Button variant="outline-primary" as={Link} to={'/developer/clients/' + client.clientId}>Edit</Button></td>
        </tr>
      )})}
      </tbody>
    </Table>
    <Button as={Link} to='/developer/clients/new'>New</Button>
    </React.Fragment>
  );
}
