import React, { useEffect, useState } from 'react';
import { useLocalStorage } from '../useLocalStorage';
import { useSelector } from 'react-redux';

export default function ImageWrapper({instanceId, isThumbnail }) {
  const [token, setToken] = useLocalStorage('token', "");
  const siteRegion = useSelector((state) => state.site.region);
  const [imageData, setImageData] = useState('/text.png');

  var query = ';'
  if(isThumbnail)
    query = '?width=200';

  useEffect(() => {
    var regionurl = null;
    switch(siteRegion) {
      case 'us':
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL + '/api/instances/' + instanceId + '/jpeg' + query;
        break;
      case 'tw':
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL_TW + '/api/instances/' + instanceId + '/jpeg' + query;
        break;
      default:
        regionurl = process.env.REACT_APP_ESPRESSOAPI_URL + '/api/instances/' + instanceId + '/jpeg' + query;
        break;
    }

    fetch(regionurl, { headers: { Authorization: "Bearer " + token }})
    .then(response => { return Promise.all([response.blob(), response])})
    .then(([responseblob, response]) => {
      if(!response.ok) {
        throw(response.status);
      }

      return responseblob;
    })
    .then(image => {
      const localUrl = URL.createObjectURL(image);
      setImageData(localUrl);
    })
    .catch(function(err) {
      //toast.error(err);
    })

  }, [instanceId, query, siteRegion, token]);

  return(
    <React.Fragment>
    { isThumbnail ? <img width={200} height={200} src={imageData} alt="dicom" /> : <img src={imageData} alt="dicom"/> }
    </React.Fragment>
  )
}
