import React from 'react';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export default function ClientFields(props) {
  return (
    <React.Fragment>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Name</Form.Label>
        <Col sm={9}><Form.Control type="text" name="name" value={props.name} onChange={(event) => {props.setname(event.target.value)}} required /></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Homepage</Form.Label>
        <Col sm={9}><Form.Control type="text" name="homepage" value={props.homepage} onChange={(event) => {props.sethomepage(event.target.value)}}/></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Description</Form.Label>
        <Col sm={9}><Form.Control as="textarea" rows={5} name="description" value={props.description} onChange={(event) => {props.setdescription(event.target.value)}}/></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Client Type</Form.Label>
        <Col sm={9}>
          <Form.Check type="radio" name="clientType" value="0" onChange={(event) => {props.setclientType(parseInt(event.target.value))}} checked={props.clientType.toString() === "0"} label={`Server side web application`} />
          <Form.Check type="radio" name="clientType" value="1" onChange={(event) => {props.setclientType(parseInt(event.target.value))}} checked={props.clientType.toString() === "1"} label={`Client side web application (e.g. single page application)`} />
          <Form.Check type="radio" name="clientType" value="2" onChange={(event) => {props.setclientType(parseInt(event.target.value))}} checked={props.clientType.toString() === "2"} label={`Native application (including mobile apps with custom URL Scheme)`} />
          <Form.Check type="radio" name="clientType" value="3" onChange={(event) => {props.setclientType(parseInt(event.target.value))}} checked={props.clientType.toString() === "3"} label={`Client side web application using pkce`} />
        </Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Oauth2 callbackurl</Form.Label>
        <Col sm={9}>
          <Form.Control type="text" name="oauth2callback" value={props.callbackurl} onChange={(event) => {props.setcallbackurl(event.target.value)}}/>
          <Form.Text className="text-muted">For native application, use custom uri or http://127.0.0.1. Port can be omitted if the client listens on a standard or a dynamic port.</Form.Text>
        </Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Organization</Form.Label>
        <Col sm={9}><Form.Control type="text" name="organization" value={props.organization} onChange={(event) => {props.setorganization(event.target.value)}}/></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Organization website</Form.Label>
        <Col sm={9}><Form.Control type="text" name="organizationHomepage" value={props.organizationHomepage} onChange={(event) => {props.setorganizationHomepage(event.target.value)}}/></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Study Url</Form.Label>
        <Col sm={9}>
          <Form.Control type="text" name="studyUrl" value={props.studyUrl} onChange={(event) => {props.setstudyUrl(event.target.value)}}/>
          <Form.Text className="text-muted">{`Per-study url to launch this app. If this field is filled, your tool will show up in the tools section for each study. The following strings will be replace with the corresponding data: {patientStudyId}, {StudyInstanceUID}, {siteId}, {AccessionNumber}, {PatientID}`}</Form.Text>
        </Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Installation Url</Form.Label>
        <Col sm={9}>
          <Form.Control type="text" name="installationUrl" value={props.installationUrl} onChange={(event) => {props.setinstallationUrl(event.target.value)}}/>
          <Form.Text className="text-muted">{`A url to install this app. For example for server side apps, it should start the oauth2 process by redirecting to /oauth2/authorize with response_type=code. If you enter an url, your tool will show up in the Marketplace.`}</Form.Text>
        </Col>
      </Form.Group>
    </React.Fragment>
  );
}
