import React from 'react';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export default function SiteFields(props) {
  return (
    <React.Fragment>
      <Form.Group as={Row} className="mb-3" >
        <Form.Label column sm="3" className="text-end">Site Name</Form.Label>
        <Col sm={9}><Form.Control type="text" name="name" value={props.name} onChange={(event) => {props.setname(event.target.value)}} /></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">City</Form.Label>
        <Col sm={9}><Form.Control type="text" name="city" value={props.city} onChange={(event) => {props.setcity(event.target.value)}}/></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">Region</Form.Label>
        <Col sm={9}><Form.Select size="lg" disabled={props.disabledRegion ? true : false} value={props.region} onChange={(event) => {props.setregion(event.target.value)}}>
          <option key='us' value='us'>USA (us)</option>
          <option key='tw' value='tw'>Taiwan (tw)</option>
        </Form.Select></Col>
      </Form.Group>
    </React.Fragment>
  );
}
