import React, { useEffect, useState, useCallback } from 'react';
import { useParams } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import InputGroup from 'react-bootstrap/InputGroup';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import ClientFields from './client-fields';

function ClientParams({clientId}) {
  const [token, setToken] = useLocalStorage('token', "");
  const [name, setname] = useState("");
  const [homepage, sethomepage] = useState("");
  const [description, setdescription] = useState("");
  const [clientType, setclientType] = useState(0);
  const [callbackurl, setcallbackurl] = useState("");
  const [organization, setorganization] = useState("");
  const [organizationHomepage, setorganizationHomepage] = useState("");
  const [studyUrl, setstudyUrl] = useState("");
  const [installationUrl, setinstallationUrl] = useState("");
  const [showclientSecret, setshowclientSecret] = useState(false);
  const [clientSecret, setclientSecret] = useState("");

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/clients/' + clientId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      setname(response.client.name);
      sethomepage(response.client.homepage);
      setdescription(response.client.description);
      setclientType(response.client.clientType);
      setcallbackurl(response.client.callbackurl);
      setorganization(response.client.organization);
      setorganizationHomepage(response.client.organizationHomepage);
      setstudyUrl(response.client.studyUrl);
      setinstallationUrl(response.client.installationUrl);
      setclientSecret(response.client.clientSecret);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [clientId, token]);

  const navigate = useNavigate();

  const updateClient = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/clients/' + clientId, { body: JSON.stringify({clientId, name, homepage, description, clientType, callbackurl, organization, organizationHomepage, studyUrl, installationUrl}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Client updated.");

      navigate('/developer/clients')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [clientId, name, homepage, description, clientType, callbackurl, organization, organizationHomepage, studyUrl, installationUrl, token, navigate]);


  const deleteClient = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/clients/' + clientId, { headers: { Authorization: "Bearer " + token }, method: 'DELETE'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Client deleted.");

      navigate('/developer/clients')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [clientId, token, navigate]);

  return (
    <React.Fragment>
    <h3>Client</h3>
    <Form>
      <ClientFields name={name} setname={setname} homepage={homepage} sethomepage={sethomepage} description={description}
        setdescription={setdescription} clientType={clientType} setclientType={setclientType} callbackurl={callbackurl}
        setcallbackurl={setcallbackurl} organization={organization} setorganization={setorganization} organizationHomepage={organizationHomepage}
        setorganizationHomepage={setorganizationHomepage} studyUrl={studyUrl} setstudyUrl={setstudyUrl} installationUrl={installationUrl}
        setinstallationUrl={setinstallationUrl} clientId={clientId} clientSecret={clientSecret}/>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">clientId</Form.Label>
        <Col sm={9}><Form.Control type="text" value={clientId} disabled/></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Form.Label column sm="3" className="text-end">clientSecret</Form.Label>
        <Col sm={9}><InputGroup><Button onClick={() => {setshowclientSecret(!showclientSecret) }}>Show Secret</Button><Form.Control type={showclientSecret ? "text" : "password"} value={clientSecret} disabled/></InputGroup></Col>
      </Form.Group>
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); updateClient(); }}>Update</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/developer/clients')}}>Cancel</Button>{' '}
          <Button variant="outline-danger" onClick={(event) => {event.preventDefault();deleteClient()}}>Delete</Button>
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}

export default function Client() {
  let params = useParams();
  return <ClientParams clientId={params.clientId} />
}
