import React, { useState, useEffect } from 'react';
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { toast } from 'react-toastify';

export function Marketplace() {
  const [token, setToken] = useLocalStorage('token', "");
  const [resultlist, setresultlist] = useState([]);

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/marketplace', { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      var result = response.apps || [];

      setresultlist(result);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token]);

  return (
    <React.Fragment>
    <h3>Apps</h3>
    <Table striped bordered>
      <thead>
        <tr>
          <th>Name</th>
          <th>Description</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
      { resultlist.map(app => { return (
        <tr key={app.clientId}>
          <td><a href={app.homepage}>{app.name}</a></td>
          <td>{app.description}</td>
          <td><Button as={Link} variant="outline-secondary" to={'/marketplace/' + app.clientId}>Details</Button></td>
        </tr>
      )})}
      </tbody>
    </Table>
    </React.Fragment>
  );
}

export function MarketplaceApp() {
  let params = useParams();
  return <MarketplaceParams clientId={params.clientId} />
}

export function MarketplaceParams({clientId}) {
  const [token, setToken] = useLocalStorage('token', "");
  const [name, setname] = useState("");
  const [homepage, sethomepage] = useState("");
  const [description, setdescription] = useState("");
  const [organization, setorganization] = useState("");
  const [organizationHomepage, setorganizationHomepage] = useState("");
  const [installationUrl, setinstallationUrl] = useState("");

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/marketplace/' + clientId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      setname(response.app.name);
      sethomepage(response.app.homepage);
      setdescription(response.app.description);
      setorganization(response.app.organization);
      setorganizationHomepage(response.app.organizationHomepage);
      setinstallationUrl(response.app.installationUrl);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [token, clientId]);

  return (
    <React.Fragment>
    <h3>Application</h3>
    <h4>{name}</h4>
    <Table>
      <tbody>
        <tr><th>Name</th><td><a href={homepage}>{name}</a></td></tr>
        <tr><th>Description</th><td>{description}</td></tr>
        <tr><th>Company</th><td><a href={organizationHomepage}>{organization}</a></td></tr>
        <tr><th></th><td><Button variant="outline-secondary" href={installationUrl}>Install</Button></td></tr>
      </tbody>
    </Table>
    </React.Fragment>
  );
}
