import './App.css';
import { BrowserRouter, HashRouter, Routes, Route } from "react-router-dom";
import LoginLogout from "./loginlogout";
import LoginOrApp from "./loginorapp";
import LoggedInLayout from "./loggedinlayout";
import { ToastContainer } from 'react-toastify';
import NotFound404 from "./routes/notfound404";
import Search from "./routes/search";
import Sites from "./routes/sites";
import Site from "./routes/site";
import SiteNew from "./routes/site-new";
import SwitchSite from "./routes/switchsite";
import StatusList from "./routes/statuslist";
import SCPs from "./routes/scps";
import SCP from "./routes/scp";
import SCPNew from "./routes/scp-new";
import Destinations from "./routes/destinations";
import Destination from "./routes/destination";
import DestinationNew from "./routes/destination-new";
import Webhooks from "./routes/webhooks";
import Webhook from "./routes/webhook";
import WebhookNew from "./routes/webhook-new";
import SiteUsers from "./routes/siteusers";
import Study from "./routes/study";
import Developer from "./routes/developer";
import Tokens from "./routes/tokens";
import Clients from "./routes/clients";
import Client from "./routes/client";
import ClientNew from "./routes/client-new";
import { Marketplace, MarketplaceApp } from "./routes/marketplace";
//import RequireLogin from "./RequireLogin";
import RequireSite from "./RequireSite";
import Applications from "./routes/applications";

import 'react-toastify/dist/ReactToastify.css';

function App() {
  return (
    <LoginOrApp>
      <HashRouter>
        <ToastContainer />
        <Routes>
          <Route path="/" element={<RequireSite><LoggedInLayout><Search /></LoggedInLayout></RequireSite>} />
          <Route>
            <Route path="/sites" element={<LoggedInLayout><Sites /></LoggedInLayout>} />
            <Route path="/sites/new" element={<LoggedInLayout><SiteNew /></LoggedInLayout>} />
            <Route path="/sites/:siteId" element={<LoggedInLayout><Site /></LoggedInLayout>} />
            <Route path="/switchsite" element={<LoggedInLayout><SwitchSite /></LoggedInLayout>} />
            <Route path="/developer" element={<LoggedInLayout><Developer /></LoggedInLayout>} />
            <Route path="/settings/tokens" element={<LoggedInLayout><Tokens /></LoggedInLayout>} />
            <Route path="/developer/clients" element={<LoggedInLayout><Clients /></LoggedInLayout>} />
            <Route path="/developer/clients/new" element={<LoggedInLayout><ClientNew /></LoggedInLayout>} />
            <Route path="/developer/clients/:clientId" element={<LoggedInLayout><Client /></LoggedInLayout>} />
            <Route path="/applications" element={<LoggedInLayout><Applications /></LoggedInLayout>} />
            <Route element={<RequireSite />}>
              <Route path="/statuslist" element={<LoggedInLayout><StatusList /></LoggedInLayout>} />
              <Route path="/scps" element={<LoggedInLayout><SCPs /></LoggedInLayout>} />
              <Route path="/scps/new" element={<LoggedInLayout><SCPNew /></LoggedInLayout>} />
              <Route path="/scps/:scpId" element={<LoggedInLayout><SCP /></LoggedInLayout>} />
              <Route path="/destinations" element={<LoggedInLayout><Destinations /></LoggedInLayout>} />
              <Route path="/destinations/new" element={<LoggedInLayout><DestinationNew /></LoggedInLayout>} />
              <Route path="/destinations/:destinationId" element={<LoggedInLayout><Destination /></LoggedInLayout>} />
              <Route path="/webhooks" element={<LoggedInLayout><Webhooks /></LoggedInLayout>} />
              <Route path="/webhooks/new" element={<LoggedInLayout><WebhookNew /></LoggedInLayout>} />
              <Route path="/webhooks/:webhookId" element={<LoggedInLayout><Webhook /></LoggedInLayout>} />
              <Route path="/siteusers" element={<LoggedInLayout><SiteUsers /></LoggedInLayout>} />
              <Route path="/studies/:patientStudyId" element={<LoggedInLayout><Study /></LoggedInLayout>} />
            </Route>
          </Route>
          <Route path="/marketplace" element={<LoggedInLayout><Marketplace /></LoggedInLayout>} />
          <Route path="/marketplace/:clientId" element={<LoggedInLayout><MarketplaceApp /></LoggedInLayout>} />
          <Route path="*" element={<LoggedInLayout><NotFound404 /></LoggedInLayout>} />
        </Routes>
      </HashRouter>
    </LoginOrApp>
  );
}

export default App;
