import React, { useEffect, useState, useCallback } from 'react';
import { useParams } from "react-router-dom";
import { useLocalStorage } from '../useLocalStorage';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import manageResponse from '../manageResponse';
import { useNavigate } from "react-router-dom";
import { toast } from 'react-toastify';
import DestinationFields from './destination-fields';

function DestinationParams({destinationId}) {
  const [token, setToken] = useLocalStorage('token', "");
  const [name, setname] = useState("");
  const [scpId, setscpId] = useState("");
  const [destinationHost, setdestinationHost] = useState("");
  const [destinationPort, setdestinationPort] = useState("");
  const [destinationAE, setdestinationAE] = useState("");
  const [sourceAE, setsourceAE] = useState("");
  const [presentationContext, setpresentationContext] = useState("");

  useEffect(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/destinations/' + destinationId, { headers: { Authorization: "Bearer " + token }})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      setname(response.destination.name);
      setscpId(response.destination.scpId);
      setdestinationHost(response.destination.destinationHost);
      setdestinationPort(response.destination.destinationPort);
      setdestinationAE(response.destination.destinationAE);
      setsourceAE(response.destination.sourceAE);
      setpresentationContext(response.destination.presentationContext);
    })
    .catch(function(err) {
      toast.error(err);
    })

  }, [destinationId, token]);

  const navigate = useNavigate();

  const updateDestination = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/destinations/' + destinationId, { body: JSON.stringify({destinationId, name, scpId, destinationHost, destinationPort, destinationAE, sourceAE, presentationContext}), headers: { Authorization: "Bearer " + token, 'Content-Type': 'application/json' }, method: 'POST'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Destination updated.");

      navigate('/destinations')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [destinationId, name, scpId, destinationHost, destinationPort, destinationAE, sourceAE, presentationContext, token, navigate]);

  const deleteDestination = useCallback(() => {
    fetch(process.env.REACT_APP_ESPRESSOAPI_URL + '/api/destinations/' + destinationId, { headers: { Authorization: "Bearer " + token }, method: 'DELETE'})
    .then((res1) => manageResponse(res1))
    .then((response) => {
      toast.success("Destination deleted.");

      navigate('/destinations')
    })
    .catch(function(err) {
      toast.error(err);
    })
  }, [destinationId, token, navigate]);

  return (
    <React.Fragment>
    <h3>Destination</h3>
    <Form>
      <DestinationFields name={name} setname={setname} scpId={scpId} setscpId={setscpId} destinationHost={destinationHost} setdestinationHost={setdestinationHost} destinationPort={destinationPort} setdestinationPort={setdestinationPort} destinationAE={destinationAE} setdestinationAE={setdestinationAE} sourceAE={sourceAE} setsourceAE={setsourceAE} presentationContext={presentationContext} setpresentationContext={setpresentationContext} />
      <Form.Group as={Row} className="mb-3">
        <Col sm="3" />
        <Col sm={9}>
          <Button variant="primary" onClick={(event) => {event.preventDefault(); updateDestination(); }}>Update</Button>{' '}
          <Button variant="outline-secondary" onClick={(event) => {event.preventDefault();navigate('/destinations')}}>Cancel</Button>{' '}
          <Button variant="outline-danger" onClick={(event) => {event.preventDefault();deleteDestination()}}>Delete</Button>
        </Col>
      </Form.Group>
    </Form>
    </React.Fragment>
  );
}

export default function Destination() {
  let params = useParams();
  return <DestinationParams destinationId={params.destinationId} />
}
